import axios from 'axios';
import { API_URL } from '../config';
/* SELECTORS */

export const getRequest = ({user}) => user.request;
export const getUser = ({user}) => user.singleObj;
export const getUserId = ({user}) => user.singleObj.id;
export const getUserCart = ({user}) => user.singleObj.cart;

/* ACTIONS */

const reducerName = 'user';
const createActionName = name => `app/${reducerName}/${name}`;

const START_REQUEST = createActionName('START_REQUEST');
const END_REQUEST = createActionName('END_REQUEST');
const ERROR_REQUEST = createActionName('ERROR_REQUEST');
const GET_USER_BY_LOGIN = createActionName('GET_USER_BY_LOGIN');

/* ACTION CREATORS */

export const startRequest = () => ({ type: START_REQUEST });
export const endRequest = () => ({ type: END_REQUEST });
export const errorRequest = error => ({ error, type: ERROR_REQUEST });

export const getUserByLogin = payload => ({ payload, type: GET_USER_BY_LOGIN });


/* THUNK CREATORS */

export const fetchUserByLogin = ( login ) => {
    return async dispatch => {
        dispatch(startRequest());
        try {
            let res = await axios.get(`${API_URL}/users/login/${login}`);
            dispatch(getUserByLogin(res.data));            
            dispatch(endRequest());
        } catch (error) {
            dispatch(errorRequest({name: 'GET_PRODUCT_BY_ID', error: error.message}));
        }
    }
} 


/* INITIAL STATE */

const initialState = {
    singleObj: {},
    request: { pending: false, error: null, success: null }
};


/* REDUCER */

export default function reducer(state = initialState, action) {
    switch (action.type) {
        case GET_USER_BY_LOGIN:
            return { ...state, singleObj: action.payload };
        case START_REQUEST:
            return { ...state, request: { ...state.request, pending: true, success: null } };
        case END_REQUEST:
            return { ...state, request: { ...state.request, pending: false, success: true } };
        case ERROR_REQUEST:
            return { ...state, request: { ...state.request, error: action.error, success: false } };
        default:
            return state;
    }
}