import { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { useForm } from "react-hook-form";

import { fetchCartById, getRequest, getCart, clearCartItemsThunk } from '../../../redux/cartRedux';
import { addOrderThunk } from '../../../redux/orderRedux';

import HeaderSection from '../../common/HeaderSection/HeaderSection';
import PopUp from '../../common/PopUp/PopUp';
import Button from '../../common/Button/Button';
import ProductCart from '../../common/ProductCart/ProductCart';
import LoadingPage from '../../common/LoadingPage/LoadingPage';

import styles from './CheckoutPage.module.scss';

const CheckoutPage = () => {
  const { register, handleSubmit, formState: { errors } } = useForm();
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const request = useSelector(getRequest);
  const cardId = JSON.parse(localStorage.getItem('cartID'));
  const [showPopUp, setShowPopUp] = useState(false);

  useEffect(() => {
    dispatch(fetchCartById(cardId));
  }, [cardId, dispatch]);

  const cart = useSelector(getCart);
  const cartItems = cart.items || [];

  const calculateTotal = () => {
    let total = 0;
    const shipping = 10;
    cartItems.forEach(product => {
      total += product.price * product.quantity;
    });
    total += shipping;
    return total.toFixed(2);
  };

  const onSubmit = async (data) => {
    const orderItems = cartItems.map(product => ({
      productId: product.id,
      color: product.color,
      quantity: product.quantity,
      comment: product.comment
    }));

    const addressString = [
      data.street,
      data.city,
      data.country,
      data.postalCode,
      data.address,
      data.fullName
    ];

    const total = parseFloat(calculateTotal());

    const order = {
      userId: cart.userId,
      items: orderItems,
      address: addressString,
      total: total,
      status: 'PENDING',
    };

    try {
      await dispatch(addOrderThunk(order));
      setShowPopUp(true);
    } catch (error) {
      console.error(error);
    }
  };

  async function closePopUp() {
    setShowPopUp(false);
    await dispatch(clearCartItemsThunk(cart.id));
    navigate('/');
  }

  if (request.pending) return <LoadingPage />;
  else if (request.error) return <>{request.error.message}</>;
  else if (!request.success) return <h1>No products</h1>;

  return (
    <>
      <section className={styles.root}>
        <HeaderSection title="Checkout" type="checkout" />
        <section className={styles.container}>
          <form className={styles.form} onSubmit={handleSubmit(onSubmit)}>
            <fieldset className={styles.wrapper}>
              <fieldset className={styles.order}>
                <legend>Order</legend>
                <ul className={styles.orderList}>
                  {cartItems.map(product => (
                    <li key={product.id}>
                      <ProductCart product={product} option="checkout" />
                    </li>
                  ))}
                </ul>
              </fieldset>

              <fieldset className={styles.summaryPrice}>
                <legend>Summary</legend>
                <ul className={styles.summaryPriceItem}>
                  <li><span>Name:</span><span>Price:</span><span></span><span>Quantity:</span></li>
                  {cartItems.map(product => (
                    <li key={product.id}>
                      <span>{product.name}</span>
                      <span>${product.price}</span>
                      <span>x</span>
                      <span>{product.quantity}</span>
                    </li>
                  ))}
                  <li><span>Delivery:</span><span>$10</span></li>
                  <li className={styles.total}><span>Total:</span>${calculateTotal()}</li>
                </ul>
              </fieldset>

              <fieldset className={styles.contact}>
                <legend>Address</legend>

                <label>
                  <span>Full Name</span>
                  <input
                    {...register("fullName", { 
                      required: "Full name is required",
                      minLength: {
                        value: 3,
                        message: "Full name must be at least 3 characters long"
                      }
                    })}
                    type="text"
                  />
                  {errors.fullName && <p className={styles.error}>{errors.fullName.message}</p>}
                </label>

                <label>
                  <span>Address</span>
                  <input
                    {...register("address", { 
                      required: "Address is required",
                      minLength: {
                        value: 3,
                        message: "Address must be at least 3 characters long"
                      }})}
                    type="text"
                  />
                  {errors.address && <p className={styles.error}>{errors.address.message}</p>}
                </label>

                <label>
                  <span>City</span>
                  <input
                    {...register("city", { 
                      required: "City is required",
                      minLength: {
                        value: 3,
                        message: "City must be at least 3 characters long"
                      }})}
                    type="text"
                  />
                  {errors.city && <p className={styles.error}>{errors.city.message}</p>}
                </label>

                <label>
                  <span>Country</span>
                  <input
                    {...register("country", { 
                      required: "Country is required",
                      minLength: {
                        value: 3,
                        message: "Country must be at least 3 characters long"
                      }})}
                    type="text"
                  />
                  {errors.country && <p className={styles.error}>{errors.country.message}</p>}
                </label>

                <label>
                  <span>Postal Code</span>
                  <input
                    {...register("postalCode", {
                      required: "Postal code is required",
                      pattern: {
                        value: /^[0-9]{2}-[0-9]{3}$/,
                        message: "Postal code must be in format 00-000"
                      }
                    })}
                    type="text"
                  />
                  {errors.postalCode && <p className={styles.error}>{errors.postalCode.message}</p>}
                </label>

                <label>
                  <span>Street</span>
                  <input
                    {...register("street", { 
                      required: "Street is required",
                      minLength: {
                        value: 3,
                        message: "Street must be at least 3 characters long"
                      }
                     })}
                    type="text"
                  />
                  {errors.street && <p className={styles.error}>{errors.street.message}</p>}
                </label>
              </fieldset>

              <fieldset className={styles.actionBtns}>
                <Button type="submit" variant="normal">Order</Button>
              </fieldset>
            </fieldset>
          </form>
        </section>
      </section>

      <PopUp
        showPopUp={showPopUp}
        closePopUp={closePopUp}
        back={true}
        close={false}
        title="Success"
      >
        Order placed successfully
      </PopUp>
    </>
  );
};

export default CheckoutPage;