//React
import { useState } from 'react'
import { useParams } from 'react-router-dom'

import { getSearchProducts, getRequest } from '../../../redux/productRedux'
import { useSelector } from 'react-redux'
//Style
import styles from './SearchPage.module.scss'

//Components
import Products from '../Products/Products'
import HeaderSection from '../../common/HeaderSection/HeaderSection'
import LoadingPage from '../../common/LoadingPage/LoadingPage'

const SearchPage = () => {
    const { searchPhrase } = useParams()
    const products = useSelector(getSearchProducts);
    const request = useSelector(getRequest);
    const [renderOption, setRenderOption] = useState('grid');
    const handleGrid = () => setRenderOption('grid');
    const handleList = () => setRenderOption('list');
    if(!searchPhrase || searchPhrase === '') return <h1 className={styles.noSearch}>You need to search something!</h1>
    return (
        <div className={styles.root}>
            <HeaderSection title={`You searched for:`} renderOption={renderOption} handleGrid={handleGrid} handleList={handleList} />
            <h2 className={styles.searchPhrase}>" {searchPhrase} "</h2>
            {request.pending && <h1 className={styles.loading}><LoadingPage /></h1>}
            {request.error && !request.success && <h1>{request.error.message}</h1>}
            {products.length === 0 && !request.pending && <h1 className={styles.noProducts}>No products</h1>}
            {request.success && products.length > 0 && <div className={styles.container}>
                <Products renderOption={renderOption} products={products}/>
            </div>}
        </div>
    )
}

export default SearchPage