import { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { MAIN_URL } from '../../../config'
//Components
import Button from '../../common/Button/Button';
import RatingRender from '../RatingRender/RatingRender';

//Styles
import styles from './ProductBox.module.scss';

const ProductBox = ({ product, addClass }) => {
    const navigate = useNavigate();
    const [isHovered, setIsHovered] = useState(false);
    const productImage = product.images[0];

    const handleClick = () => {
        navigate(`/product/${product.id}`);
    };

    console.log(`${MAIN_URL}/public${productImage}`);

    return (
    <div id={product.id} className={`${styles.root} ${styles[addClass]}`+ (isHovered ? ` ${styles.hovered}` : '')} >
        <div className={styles.image} onClick={handleClick} onMouseEnter={() => setIsHovered(true)} onMouseLeave={() => setIsHovered(false)}>
         <img src={`${MAIN_URL}/public${productImage}`} alt={product.title} /> 
        </div>
        <div className={styles.content}>
            <div className={styles.rating}>
                <RatingRender rating={product.rating} variant={'stars'} option={'stars'} />
            </div>
            <Link to={`/product/${product.id}`} onMouseEnter={() => setIsHovered(true)} onMouseLeave={() => setIsHovered(false)}>
             <h3 className={styles.title}>{product.title}</h3> 
            </Link>
            <p className={styles.shortDesc}>
                { addClass === 'grid' && product.shortDesc}
                { addClass === 'list' && product.description}
            </p>
            <p className={styles.priceContainer}>
                <span className={styles.price}>{`$${product.price}`}</span>
            </p>
            <Button type="button" variant={'other'} styles={'viewMore'} onClick={handleClick}>View more</Button>
        </div>
    </div>
    );
};

export default ProductBox;