//Styles
import styles from './HomePage.module.scss'
import Button from '../../common/Button/Button'
import { MAIN_URL } from '../../../config'

const HomePage = () => {

    return (
        <section className={styles.root}>
            <div className={styles.hero}>
                <img  src={`${MAIN_URL}/public/images/hero.jpg`} alt="hero" />
            </div>
            <div className={styles.content}>
                <h1>Support us by buying our awesome <strong>merch</strong></h1>
                <p>Explore our products and buy what you like there is allways something <strong>new!</strong></p>
                <Button type='link' LinkTo='/shop'>Shop now</Button>
            </div>
        </section>
    )
}

export default HomePage