import styles from './ProductPage.module.scss'
import { MAIN_URL } from '../../../config'

import { useParams } from 'react-router-dom'

import { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';

import { getRequest, getProduct, fetchProductById } from '../../../redux/productRedux';

import RatingRender from '../../common/RatingRender/RatingRender';
import Button from '../../common/Button/Button';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus, faMinus } from '@fortawesome/free-solid-svg-icons';

import LoadingPage from '../../common/LoadingPage/LoadingPage';

import PopUp from '../../common/PopUp/PopUp';

import { addCartItemThunk } from '../../../redux/cartItemRedux';
import Slider from "react-slick";
// Import css files
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const ProductPage = () => {
    const dispatch = useDispatch();

    const { id } = useParams();
    const request = useSelector(getRequest);
    const product = useSelector(getProduct);

    const [ quantity, setQuantity ] = useState(1);
    const [ colorCode, setColorCode ] = useState(' ');
    const [ showPopUp, setShowPopUp ] = useState(false);

    useEffect(() => {
        dispatch(fetchProductById(id));
    }, [dispatch, id]);

    useEffect(() => {
        if (product && product.colors && product.colors.length > 0) {
        setColorCode(product.colors[0]);
        }
    }, [product]);

    const settings = {
        dots: true,
        fade: false,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        waitForAnimate: false
    };

    async function handleSubmit(e) {
        e.preventDefault();
        try {
            const cartId = await JSON.parse(localStorage.getItem('cartID')) || {};
            const cartItem = {
                productId: product.id,
                cartId: cartId,
                quantity: quantity,
                color: colorCode,
            }
            dispatch(addCartItemThunk(cartItem));
            setShowPopUp(true);
        } catch (error) {
            console.error(error);
        }
    }

    const handleQuantity = (e) => {

        if(e.target.value === 'add' && quantity === 10) return;
        if(e.target.value === 'add') setQuantity(quantity + 1);
        if(quantity === 1 && e.target.value === 'sub') return;
        if(e.target.value === 'sub') setQuantity(quantity - 1);
    }

    if(request.pending) return <LoadingPage />;
    else if(!product || !product.images) return <LoadingPage/>
    else if(request.error) return <>{request.error.message}</>;
    else if(!request.success) return <h1>No product</h1>;
    else if(request.success)return(
        <>
        <section className={styles.root} id={id}>
                <article className={styles.product}> 
                    <section className={styles.info}>
                        <div key={product.id} className={`slider-container ${styles.imagesContainer}`}>
                            <sapn style={{opacity: '0', userSelect: 'none', pointerEvents: 'none'}}>{product.images[0]}</sapn>
                            <Slider {...settings}>
                                {product.images.map(image => 
                                    <div key={image} className={styles.image}>
                                        <img src={`${MAIN_URL}/public${image}`} alt={product.title} />
                                    </div>
                                )}
                            </Slider>

                        </div>
                        <div className={styles.content}>

                            <header className={styles.productHeader}> 
                                <h2>{product.title}</h2>
                                <div>
                                    <RatingRender rating={product.rating} variant={'stars'} option={'stars'} />
                                </div>
                                 <p className={styles.priceContainer}>
                                    <span className={`${styles.price}`}>
                                        ${product.price}
                                    </span>
                                </p>
                            </header>

                            <div className={styles.colors}>
                                <ul>
                                    <li>
                                        <label data-clr={product.colors[0]} className={product.colors[0] === colorCode ? styles.active : ''}>
                                            <input type="radio" name="color" id={product.colors[0]} value={product.colors[0]} onChange={(e) => setColorCode(e.target.value)}/>
                                        </label>
                                    </li>
                                    {product.colors.slice(1).map(color => 
                                        <li key={color}>
                                            <label data-clr={color} className={color === colorCode ? styles.active : ''}>
                                                <input type="radio" name="color" id={color} value={color} onChange={(e) => setColorCode(e.target.value)}/>
                                            </label>
                                        </li>
                                    )}
                                </ul>
                            </div>

                            <p className={styles.description}><span>Description:</span> {product.description}</p>

                            <form className={styles.actions} action="#" onSubmit={handleSubmit}>
                                <Button type="submit" variant={'normal'}> <FontAwesomeIcon icon={faPlus} style={{ pointerEvents: 'none' }} /> Add to cart</Button>
                                <div className={styles.quantity}>
                                    <button type='button' value='add' onClick={handleQuantity}>
                                        <FontAwesomeIcon icon={faPlus} style={{ pointerEvents: 'none' }} />
                                    </button>
                                    
                                    <input type="number" name="quantity" id="quantity" min="1" max="10" value={quantity} onChange={(e) => setQuantity(e.target.value)} />
                                    
                                    <button type='button' value='sub' onClick={handleQuantity}>
                                        <FontAwesomeIcon icon={faMinus} style={{ pointerEvents: 'none' }} />
                                    </button>
                                </div>

                            </form>

                        </div>
                    </section>
                </article>  
        </section>
        <PopUp showPopUp={showPopUp} title="Product added to cart" back={true} close={true} closePopUp={() => setShowPopUp(false)}>
            <div>
                <img src={`${MAIN_URL}/public${product.images[0]}`} alt={product.title}/>
            </div>
            <p><span>Product:</span> {product.title}</p>
            <p><span>Price:</span> ${product.price}</p>
            <p><span>Quantity:</span> {quantity}</p>
            <p><span>Color:</span> {colorCode}</p>
        </PopUp>
        </>
    )
}

export default ProductPage