import { useState } from 'react';
import { useDispatch } from 'react-redux';

import { MAIN_URL } from '../../../config'

import { deleteCartItemThunk } from '../../../redux/cartItemRedux';
import { updateCartItemByID } from '../../../redux/cartRedux';
import { updateCartItemThunk } from '../../../redux/cartItemRedux';

import styles from './ProductCart.module.scss';
import Button from '../../common/Button/Button';
import PopUp from '../PopUp/PopUp';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrash, faPen, faSave } from '@fortawesome/free-solid-svg-icons';

const ProductCart = ({product, option, onDelete, itemID}) => {
    const dispatch = useDispatch();
    const [quantity, setQuantity] = useState(product.quantity);
    const [comment, setComment] = useState(product.comment || '');

    const [ isEditable, setIsEditable ] = useState(false);

    const [ showPopUp, setShowPopUp ] = useState(false);
    const [ confirmMessage, setConfirmMessage ] = useState(false);

    const handleSave = () => {
        dispatch(updateCartItemByID({ id: itemID, quantity: parseInt(quantity), comment: comment }));
        dispatch(updateCartItemThunk({ 
            id: itemID, 
            payload: { 
                quantity: parseInt(quantity), 
                comment: comment 
            }}));
        setIsEditable(!isEditable);
    }

    const handlePopUp = () => {
        setConfirmMessage(true);
        setShowPopUp(false);
        dispatch(deleteCartItemThunk(itemID));
        onDelete();
    }

    const handleEdit = () => {
        setIsEditable(!isEditable);
    }

    const handleClick = () => {
        setShowPopUp(true);
    }


    return (
        <>
            <div className={`${styles.root} ${styles[option]}`}>
                
                <div className={styles.productImage}><img src={`${MAIN_URL}/public${product.image}`} alt={product.title}/></div>
                <div className={styles.productInfo}>
                    <h3>{product.name}</h3>
                    <p><span>Color:</span>{product.color}</p>
                </div>
                <p className={styles.price}>
                    <span className={`${styles.price} ${styles.onSale}`}>
                        ${product.price.toFixed(2)}
                    </span>
                </p>
                <label className={`${styles.quantity}`}>
                    <span>Quantity:</span>
                    { option === 'checkout' && <span className={styles.fakeInput}>{quantity}</span>}
                    { option === 'cart' && <input disabled={!isEditable} type="number" name="quantity" id="quantity" min="1" max="10" value={quantity} onChange={(e) => setQuantity(e.target.value)}/>}
                </label>
                <label className={`${styles.comment}`}> 
                    <span>Your Comment</span>
                    { option === 'checkout' && <span className={styles.fakeInput}>{comment.length > 0 ? comment : 'No comment'}</span>}
                    { option === 'cart' && <input disabled={!isEditable} type="text" name="comment" id="comment" placeholder="Your Comment" value={comment} onChange={(e) => setComment(e.target.value)}/>}
                </label>
                <div className={styles.actions}>
                    { option === 'cart' && isEditable && <Button type="button" onClick={handleSave} variant={'normal'}><FontAwesomeIcon icon={faSave} /></Button>}
                    { option === 'cart' && !isEditable && <Button type="button" onClick={handleEdit} variant={'normal'}><FontAwesomeIcon icon={faPen} /></Button>}
                    { option === 'cart' && <Button type='button' onClick={handleClick} variant={'normal'}><FontAwesomeIcon icon={faTrash} /></Button>}
                </div>
            </div>
            <PopUp showPopUp={showPopUp} close={false} back={false} closePopUp={() => setShowPopUp(false)} confirmMessage={confirmMessage} setConfirmMessage={setConfirmMessage}>
                <p>Are you sure you want to delete this item?</p>
                <div className={styles.actions}>
                    <Button type="button" onClick={handlePopUp} variant={'normal'}>Yes</Button>
                    <Button type="button" onClick={() => setShowPopUp(false)} variant={'normal'}>No</Button>
                </div>
            </PopUp>
        </>
    )
}

export default ProductCart
