import styles from './RatingRender.module.scss'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faStar, faStarHalf } from '@fortawesome/free-solid-svg-icons'
import { faStar as faStarEmpty } from '@fortawesome/free-regular-svg-icons' 
const RatingRender = ({ rating, variant, option }) =>{

    const maxRating = 5;

    function starsRender(rating){
        let stars = []
        for(let i = 1; i <= rating; i++){
            stars.push(<FontAwesomeIcon key={i} icon={faStar} />)
        }
        if(rating % 1 !== 0){
            stars.push(<FontAwesomeIcon key={stars.length + 0.5} icon={faStarHalf} />)
        }
        return stars
    }

    function starsRenderEmpty(){
        let stars = []
        for(let i = 1; i <= maxRating; i++){
            stars.push(<FontAwesomeIcon key={i} icon={faStarEmpty} />)
        }
        return stars
    }
    
    if(option === 'stars') return(
        <div className={`${styles.root} ${styles[variant]}`}>
            <div className={styles.background}>
                {starsRenderEmpty()}
            </div>
            <div className={styles.foreground}>
                {starsRender(rating)}
            </div>
        </div>
    )
}
export default RatingRender