import styles from './Products.module.scss';

import ProductBox from '../../common/ProductBox/ProductBox';

const Products = ({ renderOption, products }) => {
    return (
        <section className={`${styles.root} ${styles[renderOption]}`}>
            {renderOption === 'grid' && products.map(product => 
                <ProductBox key={product.id} product={product} addClass='grid' />
            )}
            {renderOption === 'list' && products.map(product => 
                <ProductBox key={product.id} product={product} addClass='list' />
            )}
        </section>
    )
}

export default Products