//React
import { Link } from 'react-router-dom'
import { useState } from 'react'
//Style
import style from './Button.module.scss'

const Button = ({ variant, type, styles, LinkTo, onClick, children }) => {
    let classes = "";
    if( typeof styles === 'string' && styles.includes(' ')) {
        const styleOfBtn =  styles.split(' ');
        classes = styleOfBtn.map(className => style[className]).join(' ');
    }else {
        classes = style[styles];
    }
    const [typeOfBtn] = useState(type);
    const [variantOfBtn] = useState(variant);

    if(typeOfBtn === 'link') {
        return (
            <Link to={LinkTo} className={`${style.root} ${style[styles]}`}>
                {children}
            </Link>
        )
    }else{
        return (
            <button type={type} onClick={onClick} className={variantOfBtn !== 'normal' ? `${classes}` : ` ${style.root}`}>
                {children}
            </button>
        )
    }
}

export default Button