import axios from 'axios';
import { API_URL } from '../config';
/* SELECTORS */

export const getProducts = ({products}) => products.data;
export const getProduct = ({products}) => products.singleObj;
export const getSearchProducts = ({products}) => products.searchData;
export const getRequest = ({products}) => products.request;

/* ACTIONS */

const reducerName = 'products';
const createActionName = name => `app/${reducerName}/${name}`;
const START_REQUEST = createActionName('START_REQUEST');
const END_REQUEST = createActionName('END_REQUEST');
const ERROR_REQUEST = createActionName('ERROR_REQUEST');
const LOAD_PRODUCTS = createActionName('LOAD_PRODUCTS');
const GET_PRODUCT_BY_ID = createActionName('GET_PRODUCT_BY_ID');
const SEARCH_PRODUCTS = createActionName('SEARCH_PRODUCTS');
const CLEAR_SEARCH = createActionName('CLEAR_SEARCH');

/* ACTION CREATORS */

export const startRequest = () => ({ type: START_REQUEST });
export const endRequest = () => ({ type: END_REQUEST });
export const errorRequest = error => ({ error, type: ERROR_REQUEST });
export const loadProducts = payload => ({ payload, type: LOAD_PRODUCTS });
export const getProductById = payload => ({ payload, type: GET_PRODUCT_BY_ID });
export const searchProducts = payload => ({ payload, type: SEARCH_PRODUCTS });
export const clearSearch = () => ({ type: CLEAR_SEARCH });

/* THUNK CREATORS */

export const fetchProducts = () => {
    return async dispatch => {
        dispatch(startRequest());
        try {
            let res = await axios.get(`${API_URL}/products`);
            dispatch(loadProducts(res.data));
            dispatch(endRequest());
        } catch (error) {
            dispatch(errorRequest({name: 'LOAD_PRODUCTS', error: error.message}));
        }
    }
}

export const fetchProductById = ( id ) => {
    return async dispatch => {
        dispatch(startRequest());
        try {
            let res = await axios.get(`${API_URL}/products/${id}`);
            dispatch(getProductById(res.data));
            dispatch(endRequest());
        } catch (error) {
            dispatch(errorRequest({name: 'GET_PRODUCT_BY_ID', error: error.message}));
        }
    }
}

export const fetchSearchedProducts = (searchPhrase) => {
    clearSearch();
    return async dispatch => {
        dispatch(startRequest());
        try {
            let res = await axios.get(`${API_URL}/products/search/${searchPhrase}`);
            await dispatch(searchProducts(res.data));
            dispatch(endRequest());

        } catch (error) {
            dispatch(errorRequest({name: 'SEARCH_PRODUCTS', error: error.message}));
        }
    }
}

/* INITIAL STATE */

const initialState = {
    data: [],
    singleObj: {},
    searchData: [],
    request: { pending: false, error: null, success: null, empty: true }
};


/* REDUCER */

export default function reducer(state = initialState, action) {
    switch (action.type) {
        case CLEAR_SEARCH:
            return { ...state, searchData: [] };
        case LOAD_PRODUCTS:
            return { ...state, data: [...action.payload] };
        case GET_PRODUCT_BY_ID:
            return { ...state, singleObj: action.payload };
        case SEARCH_PRODUCTS:
            return { ...state, searchData: [...action.payload] };
        case START_REQUEST:
            return { ...state, request: { ...state.request, pending: true, success: null } };
        case END_REQUEST:
            return { ...state, request: { ...state.request, pending: false, success: true } };
        case ERROR_REQUEST:
            return { ...state, request: { ...state.request, error: action.error, pending: false, success: false } };
        default:
            return state;
    }
}