//React Router
import { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { fetchSearchedProducts } from '../../../redux/productRedux';

//Styles
import styles from './SearchBar.module.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSearch, faClose } from '@fortawesome/free-solid-svg-icons'

const SearchBar = () => {
    const [isOpen, setIsOpen] = useState(false);
    const [content, setContent] = useState(<FontAwesomeIcon icon={faSearch} />);
    const [input, setInput] = useState('');
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const handleClick = (e) => {
        setIsOpen(!isOpen);
        setContent(isOpen ? <FontAwesomeIcon icon={faSearch} /> : <FontAwesomeIcon icon={faClose} />);
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        dispatch(fetchSearchedProducts(input));
        navigate(`/search/${input}`);
        setInput('');
        setIsOpen(false);
        setContent(<FontAwesomeIcon icon={faSearch} />);
    }

    return(
        <div className={styles.root}>
            <form className={isOpen ? `${styles.form}` : `${styles.form} ${styles.hidden}`} onSubmit={handleSubmit}>
                <button type="submit" className={`${styles.btn} ${styles.search}`}><FontAwesomeIcon icon={faSearch} /></button>
                <input type="text" placeholder="Search..." value={input} onChange={(e) => setInput(e.target.value)}/>
            </form>
            <button type='button' className={isOpen ? `${styles.btn} ${styles.close}` : `${styles.btn} ${styles.closed}` } onClick={handleClick}>{content}</button>
        </div>
    );
}
export default SearchBar;
