import axios from 'axios';
import { API_URL } from '../config';

/* SELECTORS */

export const getRequest = ({cartItems}) => cartItems.request;

/* ACTIONS */

const reducerName = 'cartItem';
const createActionName = name => `app/${reducerName}/${name}`;

const ADD_CART_ITEM = createActionName('ADD_CART_ITEM');
const DELETE_CART_ITEM = createActionName('DELETE_CART_ITEM');
const UPDATE_CART_ITEM = createActionName('UPDATE_CART_ITEM');

const START_REQUEST = createActionName('START_REQUEST');
const END_REQUEST = createActionName('END_REQUEST');
const ERROR_REQUEST = createActionName('ERROR_REQUEST');

/* ACTION CREATORS */

export const addCartItem = payload => ({ payload, type: ADD_CART_ITEM });
export const deleteCartItem = payload => ({ payload, type: DELETE_CART_ITEM });
export const updateCartItem = payload => ({ payload, type: UPDATE_CART_ITEM });

export const startRequest = () => ({ type: START_REQUEST });
export const endRequest = () => ({ type: END_REQUEST });
export const errorRequest = error => ({ error, type: ERROR_REQUEST });

/* LOCAL STORAGE */

async function deleteItemFromLocalStorage( {success},id ) {
    try{
        if(!success) return;
        const cart = await JSON.parse(localStorage.getItem('cart')) || {};
            const updatedCart = {
            ...cart,
            items: cart.items.filter(item => item.id !== id),
        };
        await localStorage.setItem('cart', JSON.stringify(updatedCart));
    }catch(error){
        console.error(error);
    }
}

/* THUNK CREATORS */

export const addCartItemThunk = ( payload ) => {
    return async dispatch => {
        dispatch(startRequest());
        try{
            let res = await axios.post(`${API_URL}/cart-items`, payload);
            dispatch(addCartItem(res.data));
            dispatch(endRequest());
        }catch(error){
            dispatch(errorRequest({name: 'ADD_CART_ITEM', error: error.message}));
        }
    }
} 

export const deleteCartItemThunk = ( id ) => {
    return async dispatch => {
        dispatch(startRequest());
        try{
            let res = await axios.delete(`${API_URL}/cart-items/${id}`);
            dispatch(deleteCartItem( res.data, id));
            dispatch(endRequest());
        }catch(error){
            dispatch(errorRequest({name: 'DELETE_CART_ITEM', error: error.message}));
        }
    }
}

export const updateCartItemThunk = ({id, payload}) => {
    return async dispatch => {
        dispatch(startRequest());
        try{
            let res = await axios.put(`${API_URL}/cart-items/${id}`, payload);
            dispatch(updateCartItem(res.data));
            dispatch(endRequest());
        }catch(error){
            dispatch(errorRequest({name: 'UPDATE_CART_ITEM', error: error.message}));
        }
    }
}

/* INITIAL STATE */

const initialState = {
    request: { pending: false, error: null, success: null }
};

/* REDUCER */

export default function reducer(state = initialState, action) {
    switch (action.type) {
        case DELETE_CART_ITEM:
            return deleteItemFromLocalStorage(action.payload);
        case START_REQUEST:
            return { ...state, request: { ...state.request, pending: true, success: null } };
        case END_REQUEST:
            return { ...state, request: { ...state.request, pending: false, success: true } };
        case ERROR_REQUEST:
            return { ...state, request: { ...state.request, error: action.error, pending: false, success: false } };
        default:
            return state;
    }
}