import styles from './PopUp.module.scss';
import { useNavigate } from 'react-router-dom';

const PopUp = ({showPopUp, closePopUp, back, close, title, children})=> {

    const navigate = useNavigate();

    const handleBack = () => {
        closePopUp();
        navigate(-1);
    };

    if (!showPopUp) {return null}
    return (

        <div className={styles.root}>
            <div className={styles.card}>
                {close && <div className={styles.closeBtn}>
                    <button className={`${styles.btn} ${styles.close}`} type='button' onClick={closePopUp}>X</button>
                </div>}
                <div className={styles.content}>
                    <h3 className={styles.title}>{title}</h3>
                    {children}
                </div>
                {back && <div className={styles.actions}>
                    <button className={`${styles.btn} ${styles.back}`} type='button' onClick={handleBack}>Back</button>
                </div>}
            </div>
        </div>
    )
}

export default PopUp