import { useState } from 'react';
//Styles
import styles from './HeaderSection.module.scss';

//Icons
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faGridHorizontal, faList } from "@fortawesome/free-solid-svg-icons"

//Components
import Button from '../../common/Button/Button';

const HeaderSection = ({title, renderOption, handleGrid, handleList, type}) => {

    const [typeOfHeader] = useState(type || 'product');

    return (
        <header className={typeOfHeader !== 'product' ? `${styles.root} ${styles[type]}` : `${styles.root}`}>
            <h2>{title}</h2>
{typeOfHeader === 'product' && <nav className={styles.views}>
                <Button type='button' variant={'other'} styles={ renderOption === 'grid' ? `viewBtn active` : `viewBtn`} onClick={handleGrid}>
                    <FontAwesomeIcon icon={faGridHorizontal} />
                </Button>
                <Button type='button' variant={'other'} styles={ renderOption === 'list' ? `viewBtn active` : `viewBtn`} onClick={handleList}>
                    <FontAwesomeIcon icon={faList} />
                </Button>
            </nav>}
        </header>
    )
}

export default HeaderSection