import { useState } from 'react'

//Style
import styles from './ShopPage.module.scss'

import Products from '../Products/Products'
import LoadingPage from '../../common/LoadingPage/LoadingPage'
import HeaderSection from '../../common/HeaderSection/HeaderSection'

import { fetchProducts, getProducts, getRequest } from '../../../redux/productRedux';
import { useDispatch, useSelector } from 'react-redux'
import { useEffect } from 'react';

const ShopPage = () => {
    const dispatch = useDispatch();
    const [renderOption, setRenderOption] = useState('grid');

    useEffect(() => {
        dispatch(fetchProducts());
    }, [dispatch]);

    const request = useSelector(getRequest);
    const products = useSelector(getProducts);

    const handleGrid = () => setRenderOption('grid');
    const handleList = () => setRenderOption('list');
    
    if(request.pending) return <LoadingPage />;
    else if(request.error) return <>{request.error.message}</>;
    else if(!request.success) return <h1>No products</h1>;
    else if(request.success)return(
        <section className={styles.root}>
            <HeaderSection title='Our Products' renderOption={renderOption} handleGrid={handleGrid} handleList={handleList}/>
            <div className={styles.container}>
                <Products renderOption={renderOption} products={products}/>
            </div>
        </section>
    )
}

export default ShopPage