import { Route, Routes } from 'react-router-dom';

import { useDispatch } from 'react-redux';
import { useEffect } from 'react';
import { fetchUserByLogin } from './redux/userRedux';

//Global Styles
import 'normalize.css'
import './styles/global.scss'

import MainLayout from './components/layout/MainLayout/MainLayout';
import HomePage from './components/views/HomePage/HomePage';
import ShopPage from './components/views/ShopPage/ShopPage';
import SearchPage from './components/views/SearchPage/SearchPage';
import CartPage from './components/views/CartPage/CartPage';
import CheckoutPage from './components/views/CheckoutPage/CheckoutPage';
import ProductPage from './components/views/ProductPage/ProductPage';
import NotFoundPage from './components/views/404Page/404Page';

function App() {

  const dispatch = useDispatch();
  const userLogin = 'tester';

  useEffect(() => {
    dispatch(fetchUserByLogin(userLogin));
  },[dispatch, userLogin]);

  return (
        <MainLayout>
          <Routes>
            <Route path="/" element={<HomePage />} />
            <Route path="/cart" element={<CartPage />} />
            <Route path="/checkout" element={<CheckoutPage />} />
            <Route path="/product/:id" element={<ProductPage />} />
            <Route path="/shop" element={<ShopPage />} />
            <Route path='/search' element={<SearchPage />}/>
            <Route path="/search/:searchPhrase" element={<SearchPage />} />
            <Route path="*" element={<NotFoundPage />} />
          </Routes>
        </MainLayout>
  );
}

export default App;
