//React Router
import { Link } from 'react-router-dom';
import { useState } from 'react';

//Redux
import { useSelector } from 'react-redux';

//Selectors
import { getUser } from '../../../redux/userRedux';

//Styles
import styles from './Header.module.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCartShopping, faBars, faUser } from '@fortawesome/free-solid-svg-icons'

import SearchBar from '../../common/SearchBar/SearchBar';
import Button from '../../common/Button/Button';

const Header = () => {
  const [isOpen, setIsOpen] = useState(false);
  const handleMenu = () => setIsOpen(!isOpen);

  const user = useSelector(getUser);
  const userLogged = user?.login;
  const userCartId = user?.cart?.id;

  if (user && user.cart) { 
      localStorage.setItem('cartID', JSON.stringify(userCartId));
  }

  return(
  <header className={styles.root}>
    <div className={styles.logo}>
      <h2><Link to="/">Merch Store</Link></h2>
    </div>
      <nav className={styles.nav}>
        <Button type="button" variant={'other'} styles={'menuBtn'}><FontAwesomeIcon icon={faBars} onClick={handleMenu}/></Button>
        <ul className={ isOpen ? `${styles.navList} ${styles.active}` : `${styles.navList}`}>
          { userLogged && <li><p className={styles.user}><FontAwesomeIcon icon={faUser}>{`Logged as: ${userLogged}`}</FontAwesomeIcon>{userLogged}</p></li>}
          <li><Link to="/">Home</Link></li>
          <li><Link to="/shop">Shop</Link></li>
          <li className={styles.cart}><Link to="/cart"><FontAwesomeIcon icon={faCartShopping} /></Link></li>
          <li><SearchBar /></li>
        </ul>
    </nav>
  </header>
);}

export default Header;
