import { useNavigate } from 'react-router-dom';
import { useEffect } from 'react';

import { useSelector, useDispatch } from 'react-redux';
import { fetchCartById, getRequest, getCart } from '../../../redux/cartRedux';

import Button from '../../common/Button/Button';
import ProductCart from '../../common/ProductCart/ProductCart';
import LoadingPage from '../../common/LoadingPage/LoadingPage';
import { deleteItem } from '../../../redux/cartRedux';
import styles from './CartPage.module.scss'


const CartPage = () => {
    const navigate = useNavigate();
    const request = useSelector(getRequest);
    const dispatch = useDispatch();
    const cardId = JSON.parse(localStorage.getItem('cartID'));

    useEffect(() => {
        dispatch(fetchCartById(cardId));
    }, [cardId, dispatch]);

    const cart = useSelector(getCart);
    const cartItems = cart.items;

    const handleSubmit = (e) => {
        e.preventDefault();
        navigate('/checkout');
    }
    
    const handleDelete = (id) => {
        dispatch(deleteItem(id));
    };
    if(request.pending) return <LoadingPage />;
    else if(request.error) return <>{request.error.message}</>;
    else if(!request.success) return <h1>Error contact admin!</h1>;
    else if(request.success)return(
        <section className={styles.container}>
            <form className={styles.form} method="#" onSubmit={handleSubmit}>
                <fieldset className={styles.wrapper}>
                        <fieldset className={styles.order}>
                            <legend>Order</legend>
                            <ul className={styles.orderList}>
                                {cartItems.length === 0 && <p className={styles.empty}>Your cart is empty add something from our store</p>}
                                {cartItems.map(item => (
                                    <li key={item.id}>
                                        <ProductCart product={item} option={'cart'} onDelete={() => handleDelete(item.id)} itemID={item.id} />
                                    </li>
                                ))}

                            </ul>
                        </fieldset>

                        <fieldset className={styles.actionBtns}>
                            {cartItems.length > 0 && <Button type='submit' variant={'normal'}>Checkout</Button>}
                        </fieldset>

                </fieldset>
            </form>
        </section>
    )
}

export default CartPage