import axios from 'axios';
import { API_URL } from '../config';
/* SELECTORS */

export const getCart = ({cart}) => cart.data;
export const getRequest = ({cart}) => cart.request;

/* ACTIONS */

const reducerName = 'cart';
const createActionName = name => `app/${reducerName}/${name}`;

const START_REQUEST = createActionName('START_REQUEST');
const END_REQUEST = createActionName('END_REQUEST');
const ERROR_REQUEST = createActionName('ERROR_REQUEST');
const DELETE_ITEM = createActionName('DELETE_ITEM');
const LOAD_CART_BY_ID = createActionName('LOAD_CART_BY_ID');
const UPDATE_CART_ITEM_BY_ID = createActionName('UPDATE_CART_ITEM_BY_ID');
const CLEAR_CART = createActionName('CLEAR_CART');

/* ACTION CREATORS */

export const startRequest = () => ({ type: START_REQUEST });
export const endRequest = () => ({ type: END_REQUEST });
export const errorRequest = error => ({ error, type: ERROR_REQUEST });
export const loadCartByID = payload => ({ payload, type: LOAD_CART_BY_ID });
export const deleteItem = (id) => ({ type: DELETE_ITEM, payload: id });
export const clearCart = () => ({ type: CLEAR_CART });
export const updateCartItemByID = payload => ({ payload, type: UPDATE_CART_ITEM_BY_ID });
/* THUNK CREATORS */

export const fetchCartById = ( id ) => {
    return async dispatch => {
        dispatch(startRequest());
        try {
            let res = await axios.get(`${API_URL}/carts/${id}`);
            dispatch(loadCartByID(res.data));
            dispatch(endRequest());
        } catch (error) {
            dispatch(errorRequest({name: 'LOAD_CART_BY_ID', error: error.message}));
        }
    }
}

export const clearCartItemsThunk = ( id ) => {
    return async dispatch => {
        dispatch(startRequest());
        try {
            await axios.delete(`${API_URL}/carts/clear/${id}`);
            dispatch(clearCart());
            dispatch(endRequest());
        } catch (error) {
            dispatch(errorRequest({name: 'CLEAR_CART', error: error.message}));
        }
    }
}

/* INITIAL STATE */

const initialState = {
    data : {},
    request: { pending: false, error: null, success: null }
};


/* REDUCER */

export default function reducer(state = initialState, action) {
    switch (action.type) {
        case LOAD_CART_BY_ID:
            return { ...state, data: action.payload };
        case DELETE_ITEM:
            return {
                ...state,
                data: {
                    ...state.data,
                    items: state.data.items.filter(item => item.id !== action.payload)
                }
            };
        case UPDATE_CART_ITEM_BY_ID:
        return {
            ...state,
            data: {
            ...state.data,
            items: state.data.items.map(item =>
                item.id === action.payload.id ? { ...item, ...action.payload } : item
            ),
            },
        };
        case START_REQUEST:
            return { ...state, request: { ...state.request, pending: true, success: null } };
        case END_REQUEST:
            return { ...state, request: { ...state.request, pending: false, success: true } };
        case ERROR_REQUEST:
            return { ...state, request: { ...state.request, error: action.error, pending: false, success: false } };
        default:
            return state;
    }
}